.popular-moveis {
  display: grid;
  grid-template-columns: repeat(auto-fit , minmax(150px , 1fr));
  grid-column-gap:1rem ;
  grid-row-gap:2rem ;
}

img{
  display: inline-block;
  width: 100%;
  height: 30vh;
  object-fit: cover;
  border-radius: 1rem;
  margin-bottom: 1rem;
}

h2{
  font-size: 0.8rem;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  -webkit-line-clamp: 1;
}
.filter-container{
  margin-bottom: 2rem;
  display: flex;
  flex-wrap: wrap;
   gap: 1rem;
}

.filter-container button {
  min-width: 5rem;
  padding: 0.5rem 1rem;
  border: none;
  background: white;
  color: rgb(65, 98, 168);
  border-radius: 1rem;
  border: 2px solid rgb(65, 98, 168);
  font-weight: bold;
  cursor: pointer;
}

button.active{
  background: rgb(65, 98, 168);
  color: white;
}